import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';

const mapStateToProps = state => {
    return {
        session: state.session,
    };
}
function TabPanel(props) {
    
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};
  
function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}
  
const SignInComponent = (props) => {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    useEffect( () => {
        //if (props.session) {
        //    console.log(props.session);
        //    props.history.push({
        //        pathname: '/main',
        //    });
        //}
        if (localStorage.getItem("token")) {
                props.history.push({
                    pathname: '/main',
                });
            }
    }, [props]);

    return (
        <div>
            <Box sx={{height: 60, alignItems: 'center'}}>
                <Typography variant='h5' sx={{fontWeight: 'fontWeightMedimum'}}>
                        밥자리 로그인
                </Typography>
            </Box>
            <Box sx={{ bgcolor: 'background.default', width: '100%' }}>
                <AppBar position="static">
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        indicatorColor="secondary"
                        textColor="inherit"
                        variant="fullWidth"
                        aria-label="full width tabs example"
                    >
                        <Tab icon={<PhoneIcon />} {...a11yProps(0)} />
                        <Tab icon={<EmailIcon />} {...a11yProps(1)} />
                    </Tabs>
                </AppBar>
                <SwipeableViews
                    index={value}
                    onChangeIndex={handleChangeIndex}
                >
                    <TabPanel value={value} index={0} >
                        Item One
                    </TabPanel>
                    <TabPanel value={value} index={1} >
                        Item Two
                    </TabPanel>
                </SwipeableViews>
            </Box>
        </div>     
    );
}

const SignIn = connect(mapStateToProps)(SignInComponent);

export default SignIn;